<template>
  <div :class="$root.themeHomeChange === '1' ? 'detailsList_light' : 'detailsList'">
    <div class="header">
      <el-button @click="goHome" class="btn" >返回</el-button>
      <span class="title">{{paramsInfo.name}}</span>
    </div>
    
    <!-- 详细内容 -->
    <div class="content">
      <basicMsgTable
        ref="basicMsgTable"
        :data="basicMsgList"
        :loading="basicMsgTableLoading"
      />
      <defectTable ref="defectTable" :orderId="paramsInfo.orderId" :taskId="basicMsgList.taskId" :selectIds="selectIds" @selectChange="selectChange($event)" />
      <div v-if="selectMsgs.length != 0" class="formBox">
        <div class="rectification" v-if="stage === '取证阶段' || stage === '不整改举证'" >
          <span><strong style="color: orangered;">* </strong> 是否进行整改：</span>
          <el-radio-group v-model="isRectification" @change="rectificationChange">
            <el-radio label="isTrue">是</el-radio>
            <el-radio label="isFalse">否</el-radio>
          </el-radio-group>
        </div>
        <div class="stepBox">
          <!-- 仅供测试,正式版本将会去除 -->
          <div style="text-align: center; width: 30%; min-width: 400px;">
            <el-radio-group v-model="stage" @change="radioChange" size="mini">
              <el-radio-button label="全部"></el-radio-button>
              <el-radio-button v-for="(item, index) in radioList[isRectification]" :key="index" :label="item"></el-radio-button>
            </el-radio-group>
          </div>
          <el-steps :active="stepsList[isRectification][stage]"
                    :space="200"
                    style="margin-left: 20px; width: 60%;"
                    :key="isRectification"
          >
            <el-step v-for="(item, index) in radioList[isRectification]" :key="index" :title="item"></el-step>
          </el-steps>
        </div>
        <defectForm
          ref="defectForm"
          :stage="stage"
          :loading="formLoading"
          :isRectification="isRectification"
          :data="defectFormData"
          :defectIds="defectIds"
          :orderId="paramsInfo.orderId"
          @cancel="defectFormCancel"/>
      </div>
      <div v-else class="tipsBox">请选中所要执行的流程的缺陷</div>
    </div>
  </div>
</template>

<script>
  import basicMsgTable from '../component/basicMsgTable.vue';
  import defectTable from '../component/defect-table.vue';
  import defectForm from '../component/defectForm.vue';
  import Interface from "@/base/mixin/disposal-center.js";
  // import qs from "qs";
  export default {
    name: "detailsList",
    components: {
      basicMsgTable,
      defectTable,
      defectForm
    },
    mixins: [Interface],
    props: {},
    data(){
      return {
        selectIds: [],
        paramsInfo: {},
        isRectification: 'isTrue',
        selectMsgs: [],
        tableStatus: {},
        basicMsgTableLoading: false,
        formLoading: false,
        defectIds: [],
        processCodeOption: {
          '全部': 0,
          '取证阶段': 1,
          '确认阶段': 2,
          '整改阶段': 3,
          '修订与反馈': 4,
          '不整改举证': 5,
          '举证确认': 6
        },
        stageList: {
          isTrue : {
            0 : '取证阶段',
            1 : '取证阶段',
            2 : '确认阶段',
            3 : '整改阶段',
            // '已整改' : '整改阶段',
            4 : '修订与反馈',
            5 : '修订与反馈',
            6 : '取证阶段',
          },
          isFalse: {
            7 : '不整改举证',
            8 : '举证确认',
          }
        },
        stepsList: {
          isTrue : {
            '取证阶段' : 0,
            '确认阶段' : 1,
            '整改阶段' : 2,
            '修订与反馈' : 3,
          },
          isFalse: {
            '不整改举证' : 0,
            '举证确认' : 1,
          }
        },
        radioList: {
          isTrue: [
            '取证阶段',
            '确认阶段',
            '整改阶段',
            '修订与反馈',
          ],
          isFalse: [
            '不整改举证',
            '举证确认',
          ]
        },
        stage: '取证阶段',
        defectFormData: {},
        basicMsgList: {},
      }
    },
    created() {
      // window.console.log(window.decodeURIComponent(window.atob(this.$route.query.params)));
      this.paramsInfo = JSON.parse(window.decodeURIComponent(window.atob(this.$route.query.params)));
      this.init();
    },
    mounted() {},
    methods: {
      // 初始化
      init() {
        setTimeout(() => {
          // 获取本地缓存的数据
          let defectProcessInfo = JSON.parse(localStorage.getItem("defectProcessInfo"));
          if(defectProcessInfo && this.$route.query.isRelation == '1') {
            this.stage = defectProcessInfo.stage;
            // this.selectIds = defectProcessInfo.defectIds;
            this.defectIds = defectProcessInfo.defectIds;
            this.isRectification = defectProcessInfo.isRectification;
            this.$refs['defectTable'].toggleSelection(defectProcessInfo.defectIds);
            setTimeout(() => {
              // this.selectIds = [];
              this.defectFormData = { ...defectProcessInfo.formData, knowledgeIds: (this.$route.query.knowledgeIds ? this.$route.query.knowledgeIds.split(',') : [])};
              localStorage.removeItem('defectProcessInfo');
            }, 500)
          }
        }, 1000);
        this.getBasicInfoById();
      },
      // 通过id获取基本信息
      getBasicInfoById() {
        this.basicMsgTableLoading = true;
        this.getDisposalTaskInfo({orderId: this.paramsInfo.orderId}).then((rs)=>{
          // window.console.log(rs)
          if(rs) {
            this.basicMsgList = rs;
          } else {
            this.basicMsgList = {};
          }
        }).finally(()=>{
          this.basicMsgTableLoading = false;
        })
      },
      // 单选改变
      radioChange() {
        // this.$refs['defectForm'].resetForm();
        this.init();
      },
      // 是否整改改变
      rectificationChange(val) {
        if(val === 'isTrue') {
          this.stage = '取证阶段'
        } else {
          this.stage = '不整改举证'
        }
      },
      // 表格多选的id
      selectChange(ids) {
        window.console.log(ids);
        if(ids.length != 0) {
          // window.console.log(ids[0].id)
          this.defectIds = ids.map(item => item.id );
          
          for(let key in this.stageList) {
            if(this.stageList[key][ids[0].defectStatus]) {
              this.stage = this.stageList[key][ids[0].defectStatus];
              this.isRectification = key;
              this.selectMsgs = ids;
            }
          }
          this.getProcessInfoByIds(ids.map(item => item.id ), this.paramsInfo.orderId);
        } else {
          this.selectMsgs = [];
        }
      },
      // 通过defectId 和 orderId 及 流程阶段 获取业务流程信息
      getProcessInfoByIds(defectIds, orderId) {
        this.formLoading = true;
        
        this.getProcessDetail({defectIds, orderId, processCode: this.processCodeOption[this.stage]}).then((rs)=>{
          // window.console.log(rs, this.$refs['defectTable'].searchTableNames(rs))
          if(defectIds.length > 1 && ( rs && rs.length !== 0 )) {
            this.$message.warning(`所选缺陷清单行中  ${ this.$refs['defectTable'].searchTableNames(rs) } 已有数据，无法进行多选！ `);
            this.defectFormCancel();
            this.defectFormData = {};
          } else {
            this.defectFormData = ( rs && rs[0] ) || {};
          }
        }).catch(() => {
          this.defectFormCancel();
          // this.$message.error(`接口请求出错： ${ err.code }`);
        }).finally(()=>{
          this.formLoading = false;
        })
      },
      // 关闭defectForm 表单展示
      defectFormCancel() {
        // 刷新表格
        this.$refs['defectTable'].getTableList(true);
        
        this.$refs['defectTable'].toggleSelection();
      },
      goHome() {
        this.$router.push({
          path: '/defectView',
          query: {
            index: '4-2',
            actNav: this.$route.query.actNav
          }
        });
      }
    }
  }
</script>

<style scoped lang="scss">
  
