                // z-index: 20;
                background-color: #05163d;
                padding: 0 10px;
                font-weight: bold;
              }
              // 已完成步骤条条
              .el-step__head.is-finish {
                .el-step__line {
                  background-color: #4389F9;
                }
                .el-step__icon.is-text {
                  background-color: #4389F9;
                  color: #fff;
                  border: none;
                }
              }
              .el-step__title.is-finish {
                color: #4389F9!important;
              }
              
              // 等待中步骤条
              .el-step__head.is-wait {
                .el-step__line {
                  background-color: #66B9FF;
                }
                .el-step__icon.is-text {
                  background-color: #66B9FF;
                  color: #fff;
                  border: none;
                }
              }
              .el-step__title.is-wait {
                color: #66B9FF!important;
              }
              
              // 正在进行的步骤条步骤
              .el-step__head.is-process {
                .el-step__icon.is-text {
                  background-color: #fff;
                  color: #4389F9;
                  border: none;
                }
              }
              .el-step__title.is-process {
                color: #fff!important;
              }
            }
            
            // 管理员选择模块
            .el-radio-group {
              border: 1px solid #4389F9 ;
              // border-radius: 5px;
              .el-radio-button:not(is-active) {
                .el-radio-button__inner {
                  border-color: #041338;
                  background-color: rgba($color: #041338, $alpha: 1);
                }
              }
              .el-radio-button.is-active {
                .el-radio-button__inner {
                  border-color: #4389F9;
                  background-color: rgba($color: #4389F9, $alpha: 1);
                  color: #fff;
                }
              }
            }
          }
        }
      }
      .tipsBox {
        margin: 15px 0 ;
        padding: 15px;
        border: 1.5px solid #30b0ff;
        background: rgba($color: #0065B3, $alpha: .3);
        border-radius: 2px;
        font-size: 14px;
        font-weight: bold;
        color: #fff;
        text-align: center;
      }
    }
  }
  
  // 浅色主题
  .detailsList_light {
    width: auto;
    height: auto;
    min-width: 1250px;
    padding: 0 15px 15px 15px;
    /deep/ {
      // 单选按钮
      .el-radio-group {
        .el-radio:not(is-checked) {
          color: #333;
          .el-radio__inner {
            border: 1.5px solid #bbb;
          }
        }
        .el-radio.is-checked {
          .el-radio__inner {
            background-color: #fff!important;
            border: 1.5px solid #4389F9;
            &::after {
              width: 7px;
              height: 7px;
              // border: 2px solid #03366f;
              background-color: #4389F9;
            }
          }
        }
      }
      .el-radio__input.is-checked+.el-radio__label {
        color: #4389F9!important;
      }
      // 表单样式覆盖
      .el-form {
        .el-form-item {
          margin-bottom: 15px;
        }
      }
      // 修改按钮样式
      .el-button--primary {
        background-color: #4389F9;
        color: #ffffff;
        border-radius: 3px;
        text-align: center;
        transition: all .3s;
        border: none;
      }
      .el-steps--simple {
        // background-color: #2a5cb0;
        // 待进行步骤文字样式
        .el-step__title.is-wait {
          // color: #fff;
        }
        // 进行中步骤文字样式
        .el-step__title.is-process {
          color: #041338;
        }
        .el-step__head {
          display: flex;
          align-items: center;
        }
      }
    }
    .title {
      font-size: 14px;
      font-weight: bold;
      color: #333;
    }
    .header {
      width: calc(100%);
      height: 40px;
      padding: 5px 0px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-bottom: 1px solid #bbb;
      position: relative;
      /deep/ .el-button {
        background-color: #fff;
        color: #333;
        border: 1px solid #bbb;
        border-radius: 3px;
        text-align: center;
      }
      .btn {
        position: absolute;
        left: 0;
      }
    }
    
    .content {
      width: 100%;
      height: calc(100% - 51px);
      .formBox {
        width: 100%;
        border-top: 1.5px solid #bbb;
        margin-top: 15px;
        .rectification {
          margin: 15px 0 ;
          padding: 10px 0;
          // border-radius: 2px;
          span {
            font-size: 14px;
            font-weight: bold;
            color: #333;
            margin-right: 15px;
          }
        }
        .stepBox {
          width: 100%;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          margin-top: 20px;
          /deep/ {
            .el-step {
              width: 60%;
              position: relative;
              .el-step__title {
                position: absolute;
                top: -7px;
                left: 20px;
                // z-index: 20;
                background-color: #eef3ff;
                padding: 0 10px;
                font-weight: bold;
              }
              // 已完成步骤条条
              .el-step__head.is-finish {
                .el-step__line {
                  background-color: #4389F9;
